	module.exports = {"data":{"markdownRemark":{"html":"<h2>Convert Decimals to Fractions</h2>\n<ol>\n<li>Write down decimal number as decimal / 1</li>\n<li>Multiply the numerator and denominator by 10 for every no after the decimal point.</li>\n<li>Simplify the fraction till it becomes irreducible.</li>\n</ol>\n<h2>Examples</h2>\n<h3>Example 1</h3>\n<p>Convert 0.25 into a fraction. </p>\n<p>0.25/1 —> 25/100 —> 5/20 —> 1/4\n<br>\nThe final answer is 1/4 which is the fractional representation of decimal no 0.25</p>\n<h3>Ezxample 2</h3>\n<p>Convert 0.2 into a fraction.</p>\n<p>0.2/1 ---> x100 = 20/100 = 2/10.</p>\n<p><img src=\"http://m.mathblaster.com/Mathblaster/CoolMath/Article-Images/decimals-to-fractions-&#x26;-fractions-to-decimals.jpg\" alt=\"Examples\"></p>","fields":{"slug":"/mathematics/convert-decimals-to-fractions/"},"frontmatter":{"title":"Convert Decimals to Fractions"}}},"pathContext":{"slug":"/mathematics/convert-decimals-to-fractions/"}}

